@import url(https://fonts.googleapis.com/css?family=Montserrat);
@import url(https://fonts.googleapis.com/css?family=Montserrat);
@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.root label {
    position: relative;
}

input {
    width: 100%;
    font-size: 1rem;
    font-family: "Montserrat", sans-serif;
    color: #fff;
    border: 0;
    padding: .75rem .9375rem .9375rem;
    background: #000;
    border-radius: .3125rem .3125rem 0 0;
    text-align: center;
    box-sizing: border-box;
}

.info {
    background: #333;
    text-align: center;
    padding: 1.875rem .9375rem;
}

.info img {
    width: 6.25rem;
    height: 6.25rem;
    margin-bottom: 1rem;
    border-radius: 50%;
    display: block;
    box-shadow: 0 0 0.0625rem rgba(0, 0, 0, 0.5);
}

.info h2 a {
    background: -webkit-linear-gradient(left, #F37335, #FDC830);
    background: linear-gradient(to right, #F37335, #FDC830);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    padding: 5px;
}

.info h3 {
    font-size: 1.1885rem;
    color: #bdcbce;
}

.state {
    background: #FDC830;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #F37335, #FDC830);  /* Chrome 10-25, Safari 5.1-6 */
    background: -webkit-linear-gradient(left, #F37335, #FDC830);
    background: linear-gradient(to right, #F37335, #FDC830); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    text-align: center;
    padding: 1.875rem .9375rem;
}

.state li {
    list-style: none;
    display: inline-block;
    margin-right: 1rem;
}

.state a {
    color: #fff;
}

.state i {
    font-size: 1.5rem;
    font-weight: 700;
    font-style: normal;
    display: block;
}

.state span {
    font-size: .844rem;
    letter-spacing: .09rem;
    color: #fff;
}

.notFound {
    background: #fff;
    padding: 1.875rem;
    text-align: center;
}

.notFound h2 {
    font-size: 2rem;
    color: #2368bd;
    margin-bottom: .625rem;
}

.notFound p {
    font-size: 1rem;
    color: #a3b6bb;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
	box-sizing: border-box;
}

*:active,
*:focus {
  border: 0;
  outline: 0;
  }

body {
  font-family: 'Montserrat', sans-serif;
	padding: 20px;
	height: 100%;
  background: #DAE2F8;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #D6A4A4, #DAE2F8);  /* Chrome 10-25, Safari 5.1-6 */
background: -webkit-linear-gradient(left, #D6A4A4, #DAE2F8);
background: linear-gradient(to right, #D6A4A4, #DAE2F8); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	font-size: 14px;
	line-height: 1;
}

ul {
	padding: 0;
}

li {
	list-style: none;
}

a {
    text-decoration: none;
    display: inline-block;
}

.card {
    width: 25rem;
    margin: auto;
    overflow: hidden;
    position: absolute;
    top: 50%;
    left: 50%;
    box-shadow: 4px 2px 10px rgba(0, 0, 0, 0.3);
    border-radius: 0 0 .3125rem .3125rem;
    -webkit-transform: translateX(-50%) translateY(-50%);
            transform: translateX(-50%) translateY(-50%);
}

